<template>
    <div class="main">
        <p>标题</p>
        <input type="text" v-model="title">
        <p>内容</p>
        <textarea cols="40" rows="10" type="text" v-model="content"></textarea>
        <div class="btn">
            <button @click="add()">完成</button>
            <button @click="cancel()">取消</button>
        </div>
    </div>
</template>

<script>
    import store from '@/store'

    export default {
        name: "Add",
        store,
        data() {
            return {
                title: '',
                content: ''
            }
        },
        methods: {
            add() {
                store.commit('addItem', {
                    title: this.title,
                    content: this.content
                })
                this.title = ''
                this.content = ''
                this.$router.push('/home/list')
            },
            cancel() {
                this.$router.push('/home/user')
            }
        }
    }
</script>

<style scoped>
    .main {
        height: 100vh;
        padding: 10px;
    }

    p {
        font-size: 26px;
    }

    input,
    textarea {
        width: 98%;
        border: 1px solid grey;
        border-radius: 4px;
    }

    .btn {
        margin-top: 6%;
        height: 8%;
        display: flex;
        justify-content: center;
    }

    button {
        width: 46%;
        margin: 4px;
    }
</style>